import { FunctionComponent } from 'preact';

export interface ConditionalProps {
  initialized?: boolean;
  visible?: boolean;
}

export function WithConditionals<P extends object>(
  Component: FunctionComponent<P>,
  Skeleton: FunctionComponent
): FunctionComponent<P & ConditionalProps> {
  return function ConditionalWrapper(props: P & ConditionalProps) {
    const { initialized = true, visible = true, ...componentProps } = props;

    return (
      <div className="conditional-wrapper">
        {visible && !initialized && <Skeleton />}
        {visible && initialized && <Component {...(componentProps as P)} />}
      </div>
    );
  };
}
